$colCount: 12 !default
$grid-breakpoints: (sm: 576px, md: 768px, lg: 1024px, xl: 1280px) !default
.row
	display: flex
	flex-wrap: wrap
	margin-left: -8px
	margin-right: -8px
	@apply gap-y-4 xl:gap-y-8 2xl:gap-y-10
	@media (min-width: 1280px)
		margin-left: clamp(-16px,calc(-16/1920*100rem),calc(-16/1920*100rem))
		margin-right: clamp(-16px,calc(-16/1920*100rem),calc(-16/1920*100rem))
	@media (min-width: 1440px)
		margin-left: setClamp(-20)
		margin-right: setClamp(-20)

		// &.medium-spacing
		// 	margin-left: clamp(-10px,calc(-10/1920*100rem),calc(-10/1920*100rem))
		// 	margin-right: clamp(-10px,calc(-10/1920*100rem),calc(-10/1920*100rem))
		// 	> [class*="col-"]
		// 		@media (min-width: 1280px)
		// 			padding-right: clamp(10px,calc(10/1920*100rem),calc(10/1920*100rem))
		// 			padding-left: clamp(10px,calc(10/1920*100rem),calc(10/1920*100rem))
	> [class*="col-"]
		position: relative
		width: 100%
		padding-right: 8px
		padding-left: 8px
		@media (min-width: 1280px)
			padding-right: setClamp(16)
			padding-left: setClamp(16)
		@media (min-width: 1440px)
			padding-right: setClamp(20)
			padding-left: setClamp(20)

	.col
		-ms-flex-preferred-size: 0
		flex-basis: 0
		-ms-flex-positive: 1
		flex-grow: 1
		max-width: 100%
	&.column-full-no-gutter
		@media (max-width: 575.98px)
			@apply mx-0
			> [class*='col-']
				@apply px-0

.no-gutters
	margin-left: 0
	margin-right: 0

.p-0
	padding-left: 0
	padding-right: 0

.col-auto
	-ms-flex: 0 0 auto
	flex: 0 0 auto
	width: auto
	max-width: 100%

@for $col from 1 through $colCount
	.col-#{$col}
		-ms-flex: 0 0 (100% / $colCount * $col)
		flex: 0 0 (100% / $colCount * $col)
		max-width: (100% / $colCount * $col)

@each $bp, $value in $grid-breakpoints
	@media (min-width: #{$value})
		.col-#{$bp}
			-ms-flex-preferred-size: 0
			flex-basis: 0
			-ms-flex-positive: 1
			flex-grow: 1
			max-width: 100%
		.col-#{$bp}-auto
			-ms-flex: 0 0 auto
			flex: 0 0 auto
			width: auto
			max-width: 100%
		@for $col from 1 through $colCount
			.col-#{$bp}-#{$col}
				-ms-flex: 0 0 (100% / $colCount * $col)
				flex: 0 0 (100% / $colCount * $col)
				max-width: (100% / $colCount * $col)
