#overlay
	position: fixed
	top: 0
	left: 0
	width: 100%
	height: 100%
	@apply bg-transparent
	@apply pointer-events-none opacity-0 transition-all z-30
	&.active
		@apply pointer-events-auto opacity-100

.desktop-show
	@apply hidden xl:block
	.download
		@apply hidden
	.hotline
		@apply bg-primary-3 h-8 inline-flex items-center justify-center px-4 relative text-white
		&::before
			content: ""
			@apply rem:border-t-[16px] rem:border-b-[16px] border-t-transparent border-b-transparent
			@apply absolute right-full top-0 w-0 h-0 rem:border-r-[16px] border-r-primary-3
			@apply rem:rounded-l-sm
		&::after
			content: ""
			@apply rem:border-t-[16px] rem:border-b-[16px] border-t-transparent border-b-transparent
			@apply absolute left-full top-0 w-0 h-0 rem:border-l-[16px] border-l-primary-3
			@apply rem:rounded-l-sm
			
.mobile-show
	@apply xl:hidden block

#fixed-tool
	bottom: 140px
	z-index: 100
	@apply fixed
	@apply right-0
	@apply space-y-2 xl:space-y-5 pointer-events-none
	.item
		transform: translateX(calc(100% - 46px))
		box-shadow: setClamp(4) setClamp(4) setClamp(8) setClamp(4) rgba(0, 0, 0, 0.08)
		@apply pointer-events-auto rounded-none #{!important}
		@media (min-width: 1280px)
			transform: translateX(calc(100% - clamp(64px,calc(64/1920*100rem),calc(64/1920*100rem))))
			box-shadow: setClamp(4) setClamp(4) setClamp(8) setClamp(4) rgba(0, 0, 0, 0.24)
		&:hover
			@apply translate-x-0 bg-primary-500
		.icon
			flex: 0 0 32px
			width: 32px
			height: 32px
			@media (min-width: 1280px)
				flex: 0 0 setClamp(48)
				width: setClamp(48)
				height: setClamp(48)
			i
				@apply text-18px xl:text-32px text-white
			img
				@apply w-full h-full p-1
		.ctn
			flex: 0 0 setClamp(128)
			width: setClamp(128)
	// .scrollToTop
	// 	@apply translate-y-full opacity-0
	// 	&.active
	// 		@apply translate-y-0 opacity-100

ul
	&.list-item-added
		padding-left: clamp(19px,calc(19/1920*100rem),calc(19/1920*100rem))
		@apply list-disc

.wpforms-field-hidden,.wpforms-field-label
	@apply hidden
label.wpforms-label-hide
	@apply hidden
input:not([type="submit"])
	&.wpforms-error
		@apply bg-red-500/20 text-white

.wpcf7-not-valid-tip
	@apply text-sm text-red-500 my-2
.wpcf7
	form.wpcf7-form
		.wpcf7-response-output
			@apply m-0 my-3
			@apply border-0 text-white text-center text-sm font-semibold
			@apply rounded-4px py-3
			@apply w-fit
		&.submitting
			.button
				@apply cursor-not-allowed
			button
				@apply bg-neutral-200 pointer-events-none opacity-50
		&.unaccepted
			.wpcf7-response-output
				@apply bg-yellow-500
		&.sent
			.wpcf7-response-output
				@apply bg-green-600
		&.invalid
			.wpcf7-response-output
				@apply bg-red-500
			// .button
			// 	@apply cursor-not-allowed
			// button
			// 	@apply bg-neutral-200 pointer-events-none opacity-50
			.form-group
				.wpcf7-not-valid-tip
					@apply text-sm text-red-500 my-2
		.form-group
			&.phoneInput,&.countryInput
				input
					@apply pl-11

