.content-spacing
	* + *
		@apply mt-5

.edit-link-post
	width: setClamp(20)
	height: setClamp(20)
	@apply pointer-events-auto inline-flex items-center justify-center
	span
		@apply text-primary-400

.edit-term-post
	width: setClamp(20)
	height: setClamp(20)
	@apply pointer-events-auto inline-flex items-center justify-center
	span
		@apply text-blue-700

.ovh
	@apply overflow-hidden
.img-ratio
	position: relative
	display: block
	height: 0
	overflow: hidden
	padding-top: 100%
	img,iframe
		@apply absolute top-0 left-0 w-full h-full object-cover transition-all duration-300
.stretched-link
	&::before
		content: ''
		@apply absolute top-0 left-0 w-full h-full z-1 pointer-events-auto

.loading-container
	@apply absolute inset-0
	@apply z-2
	@apply pointer-events-none
	@apply transition-all duration-300
	@apply flex items-center justify-center
	@apply opacity-0 pointer-events-none cursor-progress
	&.active
		@apply opacity-100 pointer-events-auto
	// &::before
	// 	content: ''
	// 	@apply absolute inset-0 bg-black/40
	.loading-wrapper
		width: setClamp(60)
		padding: setClamp(2) 0
		margin: 0 -5%
		@apply flex items-center justify-center relative overflow-hidden
		span
			position: relative
			left: -9999px
			width: setClamp(10)
			height: setClamp(10)
			border-radius: setClamp(5)
			background-color: #05BD7D
			color: #05BD7D
			box-shadow: 9999px 0 0 -5px
			animation: dot-pulse 1.5s infinite linear
			animation-delay: 0.25s

			&::before, &::after
				content: ''
				display: inline-block
				position: absolute
				top: 0
				width: setClamp(10)
				height: setClamp(10)
				border-radius: setClamp(5)
				background-color: #05BD7D
				color: #05BD7D

			&::before
				box-shadow: 9984px 0 0 -5px
				animation: dot-pulse-before 1.5s infinite linear
				animation-delay: 0s

			&::after
				box-shadow: 10014px 0 0 -5px
				animation: dot-pulse-after 1.5s infinite linear

.image-svg
	img
		@apply opacity-0
	.img-generate
		img
			@apply opacity-100
	&.image-absolute
		.svg-generate
			@apply absolute inset-0 w-full h-full
		svg
			@apply w-full h-full

.expander
	display: grid
	grid-template-rows: 0fr
	overflow: hidden
	transition: grid-template-rows .3s
	.expander-content
		min-height: 0
		transition: visibility .3s
		visibility: hidden
	// .active
		// grid-template-rows: 1fr
		// .expander-content
		// 	visibility: visible

[stick-to-edge]
	@apply transition-all

.image-not-found
	@apply text-xs text-red-600

.play-btn
	.icon
		width: 50px
		height: 50px
		@apply transition-all
		@media (min-width: 768px)
			width: 70px
			height: 70px
		@media (min-width: 1280px)
			width: setClamp(110)
			height: setClamp(110)
		a
			+img-ratio(1/1)
			@apply overflow-visible
		svg
			@apply overflow-visible
		circle
			transform-origin: center
			transform-box: fill-box
			@apply transition-all
			&:nth-of-type(2)
				animation: ping 1.3s ease-in-out infinite both
			&:nth-of-type(3)
				animation: ping 1.3s .3s ease-in-out infinite both
		path
			// animation: pathMove 1.3s ease-in-out infinite both
			@apply transition-all
			@media (max-width: 767.98px)
				@apply scale-125 origin-center
		@keyframes pathMove
			0%
				stroke-dashoffset: var(--data-stroke-dasharray)
				stroke-dasharray: var(--data-stroke-dasharray)
			100%
				stroke-dasharray: 0
				stroke-dashoffset: var(--data-stroke-dasharray)
		.pause
			@apply hidden
	&:hover
		.icon
			@apply scale-90
		// path
		// 	@apply fill-primary-300

.introduce-nav
	@apply -top--header-height
	ul
		@apply list-none p-0 overflow-auto
		&::-webkit-scrollbar
			width: 0
			height: 0
		a
			@apply whitespace-nowrap
			@media (max-width: 1279.98px)
				&::before
					content: ''
					@apply absolute bottom-0 w-0 h-1px bg-primary-800 transition-all duration-500
					@apply left-1/2 -translate-x-1/2
		li
			&.active,&:hover
				a
					@apply text-primary-800 font-bold
					&::before
						@apply w-full

.normal-nav
	nav
		ul
			@apply list-none p-0 flex items-center gap-19 whitespace-nowrap overflow-auto justify-center w-full
			a
				@apply text-18px text-white py-11px px-6
				@apply bg-primary-500
				@apply  transition-all
				@apply font-bold h-12 inline-flex items-center justify-center relative
				&::before
					content: ""
					@apply rem:border-t-[24px] rem:border-b-[24px] border-t-transparent border-b-transparent
					@apply absolute right-full top-0 w-0 h-0 rem:border-r-[24px] border-r-primary-500
					@apply rem:rounded-l-sm transition-all
				&::after
					content: ""
					@apply rem:border-t-[24px] rem:border-b-[24px] border-t-transparent border-b-transparent
					@apply absolute left-full top-0 w-0 h-0 rem:border-l-[24px] border-l-primary-500
					@apply rem:rounded-l-sm transition-all
			li
				&.active,&:hover
					a
						@apply bg-primary-800 text-white
						&::before
							@apply border-r-primary-800
						&::after
							@apply border-l-primary-800

.has-youtube-video
	@apply relative
	iframe
		@apply absolute inset-0 z-1 pointer-events-none opacity-0 w-full h-full
	.video-toggle
		@apply absolute inset-0 z-2
	&.active
		.video-toggle
			@apply opacity-0 pointer-events-none
		iframe
			@apply opacity-100 pointer-events-auto
