.recruitment-3
	@apply pt-0 #{!important}
	.career-table
		&::-webkit-scrollbar
			@apply w-[calc(6/1920*100rem)] h-1
		&::-webkit-scrollbar-track
			background-color: rgba(#e5e5e5,.2 )
		&::-webkit-scrollbar-thumb
			@apply bg-primary-500
		table
			@apply table-fixed w-full
		thead
			@apply bg-primary-500
			tr
				td
					@apply border-r border-r-neutral-100 text-white  font-bold text-xl
					@apply px-5 h-12
		tbody
			tr:nth-child(even)
				@apply bg-neutral-50 px-3 py-5
			tr
				td
					@apply py-3 px-5 border-r border-r-neutral-100 border-l border-neutral-100 border-b border-b-neutral-100 text-lg  font-normal
					>a
						@apply hover:text-primary-800
					.dowload
						@apply text-primary-500 font-medium transition-all ease-in-out cursor-pointer
						i
							@apply text-xl
				td:nth-child(1)
					@apply text-neutral-950 font-bold  text-lg 
	.more
		a
			@apply bg-primary-800 relative h-10 items-center justify-center px-4 text-white flex transition-all
			&::before
				content: ""
				@apply rem:border-t-[20px] rem:border-b-[20px] border-t-transparent border-b-transparent
				@apply absolute right-full top-0 w-0 h-0 rem:border-r-[20px] border-r-primary-800
				@apply rem:rounded-l-sm transition-all
			&::after
				content: ""
				@apply rem:border-t-[20px] rem:border-b-[20px] border-t-transparent border-b-transparent
				@apply absolute left-full top-0 w-0 h-0 rem:border-l-[20px] border-l-primary-800
				@apply rem:rounded-r-3 transition-all
			&:hover
				@apply bg-primary-500
				&::before
					@apply border-r-primary-500
				&::after
					@apply border-l-primary-500
