body, html
	font-size: 14px
	@media (min-width: 576px)
		font-size: 16px
	@media(min-width: 1024px)
		font-size: 16px
	@media(min-width: 1280px)
		font-size: 1vw

main
	max-width: 100vw

::selection
	color: #fff
	@apply bg-primary-400

img
	&.noImage
		padding: 5% !important
		object-fit: contain !important
