.bn-1
	@apply rounded-3 bg-white
	@media (min-width: 1280px)
		box-shadow: 0 setClamp(4) setClamp(8) 0 rgba(0, 0, 0, 0.08)
	@media (max-width: 1279.98px)
		@apply border border-black/[0.08]
	.img
		a
			+img-ratio(180/320)
			@apply rounded-3
	&:hover
		@apply bg-neutral-50
		.title
			@apply text-primary-800
