.home-3
	.wrap-main
		.wrapper
			@screen -xl
				@apply px-5
	.wrapper-list
		ul
			li
				@apply relative
				.item
					.content
						@apply text-center px-6
						h3
							@apply rem:h-[52px] px-5 w-full bg-transparent text-center rem:text-[24px] text-neutral-900 flex justify-center items-center relative z-40
							@apply transition-all cursor-pointer
							&::before
								content: ""
								@apply rem:border-t-[26px] rem:border-b-[26px] border-t-transparent border-b-transparent
								@apply absolute right-full top-0 w-0 h-full rem:border-r-[26px]
								@apply rem:rounded-l-sm opacity-0 transition-opacity
							&::after
								content: ""
								@apply rem:border-t-[26px] rem:border-b-[26px] border-t-transparent border-b-transparent
								@apply absolute left-full top-0 w-0 h-full rem:border-l-[26px] opacity-0
								@apply rem:rounded-r-3 transition-opacity
							&:hover
								@apply bg-primary-500 text-white
								&::before
									@apply border-r-primary-500 opacity-100
								&::after
									@apply border-l-primary-500 opacity-100
				&.active
					.item
						.content
							h3
								@apply bg-primary-500 text-white
								&::before
									@apply border-r-primary-500 opacity-100
								&::after
									@apply border-l-primary-500 opacity-100

					
