@tailwind base
@tailwind components
@tailwind utilities

body
	@apply bg-white

@layer components
	*,
	*::before,
	*::after
		box-sizing: border-box

	*::-webkit-scrollbar-track
		// background: #D1D1D1
		@apply bg-neutral-100 rounded-4

	*::-webkit-scrollbar
		// width: r(10)
		width: 0
		height: setClamp(2)

	*::-webkit-scrollbar-thumb
		border-radius: setClamp(2)
		@apply bg-black/70 rounded-4
	*:focus-within,*:focus-visible
		// @apply outline-primary-500
		@apply outline-0
	html
		-webkit-text-size-adjust: 100%
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0)
		--padding-left: calc(15/1920*100rem)
		--padding-right: calc(15/1920*100rem)
		@media (min-width: 1280px)
			--padding-left: calc(100/1920*100rem)
			--padding-right: calc(100/1920*100rem)
	.container
		max-width: 100%
		@media (min-width: 1280px)
			@apply px-12 max-w-clamp-1496px
	body
		font-weight: 400
		@apply font-body text-14px md:text-18px text-black

	img
		display: inline
	.absolute-center
		@apply top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 absolute
	main
		margin-top: var(--header-height,70px)
		@media (min-width: 1024px)
			margin-top: var(--header-height,setClamp(102))
		// #page-banner
		// 	@media (min-width: 1024px)
		// 		margin-top: calc(var(--header-height,setClamp(96))*-1)
	body.home
		main
			@media (min-width: 1280px)
				margin-top: 0
	::-moz-range-track
		background: black
		border: 0
	input::-moz-focus-inner,
	input::-moz-focus-outer
		border: 0
