.news-detail
	.article-header
		time
			&::after
				content: ''
				@apply bg-primary-500 w-full h-1px static flex-1
	.other-title
		&::after
			content: ''
			@apply w-16 h-0.5 bg-primary-500 left-0 bottom-0 absolute
	.item
		& + .item
			@apply border-t border-neutral-200
		.img
			flex: 0 0 calc(174/440*100%)
			a
				+img-ratio(98/174)
				@apply rounded-2
