.global-breadcrumb
	@apply bg-neutral-50
	.rank-math-breadcrumb
		@apply mt-2
		@media (max-width: 575.98px)
			@apply hidden
		p
			@apply font-normal text-neutral-900
			@apply flex flex-wrap items-center
			@apply gap-x-0
			@apply text-18px
			a
				@apply py-2
				&:first-child
					@apply text-0
					&::after
						content: "\f015"
						@apply font-Awesome6 text-18px font-light

			.separator
				width: auto
				font-size: 0
				@apply py-0
				@apply flex items-center bg-transparent
				@apply relative
				&::before
					content: "\f105"
					@apply font-Awesome6 text-18px font-light mx-3

