.product-detail-1
	.img
		a
			+img-ratio(454/680)

.product-detail-2
	.thumb
		.swiper-slide
			@apply mr-5
			&:last-child
				@apply mr-0
		.swiper-slide
			width: calc(calc(100% - calc(10px * calc(2 - 1)))/2)
			@media (min-width: 576px)
				width: calc(calc(100% - calc(10px * calc(3 - 1)))/3)
			@media (min-width: 1024px)
				width: calc(calc(100% - calc(clamp(20px,calc(20/1920*100rem),calc(20/1920*100rem)) * calc(4 - 1)))/4)
			&.swiper-slide-thumb-active
				.img
					@apply border border-primary-600
		.img
			@apply border border-transparent
		.img
			a
				+img-ratio(94/135)
				@apply bg-neutral-50
	.main
		.img
			a
				+img-ratio(454/680)
				@apply bg-neutral-50
	.arrow-button
		@media (max-width: 1599.98px) and (min-width: 1280px)
			.swiper-button-prev
				left: -60px
			.swiper-button-next
				right: -60px
