.product-list
	.sticky-wrapper
		.sticky-nav
			@apply transition-all
		&.is-sticky
			.sticky-nav
				@apply bg-white shadow-sm py-2
	.category-info
		.img
			a
				+img-ratio(479/690)
		&:hover
			.title
				@apply text-primary-1

	.child-category
		ul
			@apply list-none p-0 overflow-auto
		li
			& + li
				a
					&::before
						content: ''
						@apply w-2 h-2 bg-primary-800
						@apply mx-6 xl:mx-8
			&.active,&:hover
				a
					@apply text-primary-800
	.category-item
		&:first-child
			@apply pt-10
		&:nth-of-type(even)
			@apply bg-[#ECFDF7]
	.images
		padding-top: calc(479/690*100%)
		.main-img
			max-width: calc(560/690*100%)
			a
				+img-ratio(374/560)
		.boards
			max-width: calc(366/690*100%)
			.wrapper
				padding-top: calc(346/366*100%)
			a
				@apply w-full h-full
				img
					@apply w-full h-full object-contain

.product-video
	.swiper-column-auto
		.img
			a
				+img-ratio(654/1400)
				@apply bg-neutral-50
	.video
		@apply opacity-0 pointer-events-none
		video
			@apply w-full h-full object-cover
	.play-btn
		@apply transition-all
	.play-video
		.video
			@apply opacity-100
		.play-btn
			.icon
				@apply opacity-0 scale-50
		.wrapper
			@apply opacity-0

.product-protocol
	.swiper
		.swiper-slide
			width: calc(50%-5px)
			@apply mr-10px
			@media (min-width: 576px)
				width: calc(33.3333% - 6px)
			@media (min-width: 768px)
				width: calc(25% - 8px)
			@media (min-width: 1280px)
				width: calc(272/1400*100%)
