.text-image
	background: url('../img/home/title-image.webp') no-repeat center bottom 39%
	background-clip: text
	-webkit-background-clip: text
	-webkit-text-fill-color: transparent
	@apply select-none bg-cover

.block-title
	@apply font-bold text-black
	@apply text-32px sm:text-40px leading-normal xl:header-48

.primary-list-color
	li
		&::marker
			@apply text-primary-800
		& + li
			@apply mt-3
