.home-5
	.wrapper
		.item-service-primary
			@apply relative
			&::before
				content: ""
				background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.70) 100%)
				@apply absolute w-full bottom-0 left-0 h-full  z-1 opacity-0 transition-opacity
			&:hover
				&::before
					@apply opacity-100
			@screen xl
				&:hover
					.content
						transform: translateY(0)
						.desc
							@apply opacity-100
				.content
					transform: translateY(var(--height-ele))
					&::before
						content: ""
						background: linear-gradient(0deg, #000 20.12%, rgba(0, 0, 0, 0.00) 100%)
						@apply absolute w-full h-full top-0 left-0 pointer-events-none -z-1
					@apply transition-all duration-300
					.desc
						@apply transition-all duration-300
						@apply opacity-0
			.content
				@screen -xl
					@apply static bg-primary-500
	.icon
		img
			@apply w-full h-full object-contain
	.swiper-column-auto
		.img
			a
				+img-ratio(810/1400)
				@apply bg-neutral-50
		.arrow-button
			.swiper-button-prev
				@apply xl:left-8 #{!important}
			.swiper-button-next
				@apply xl:right-8 #{!important}
		.swiper
			.swiper-slide
				.wrapper
					@apply bg-primary-500/90
	.video
		@apply opacity-0 pointer-events-none
		video
			@apply w-full h-full object-cover
	.play-btn
		@apply transition-all
	.play-video
		.video
			@apply opacity-100
		.play-btn
			.icon
				@apply opacity-0 scale-50
		.wrapper
			@apply opacity-0
