.search-page
	// background-color: #f0f0f2
	.search-form
		form
			@apply relative
			input
				padding: r(10) r(20)
				border: thin solid
				padding-right: r(45)
				@apply w-full bg-transparent transition-all
				&:not(:placeholder-shown)
					@apply bg-primary-400 border-transparent text-white
			button
				width: r(40)
				@apply h-full absolute top-0 right-0 flex items-center justify-center
	.search-query
		@apply my-15px
	.found-nothing
		background-image: url(../img/nothing.png)
		height: 30vh
		@apply bg-no-repeat bg-center
	.found-nothing-title
		@apply text-xl uppercase relative font-bold text-primary-400
	.list-search
		@apply grid sm:grid-cols-2 lg:grid-cols-3 lg:gap-x-6 gap-y-8 sm:gap-y-12
.wp-pagination
	@apply mt-8 xl:mt-10
	ul
		@apply list-none flex items-center flex-wrap justify-center
		@apply gap-3 sm:gap-5
		&.list-item-added
			padding-left: 0
		li
			@apply list-none
			&.next-page,&.prev-page
				@apply hidden
			// &.first-item,&.last-item
			// 	a
			// 		font-size: 0
			// 	i
			// 		@apply font-normal text-lg
			a
				width: 40px
				height: 40px
				@apply text-16px text-white
				@apply font-bold
				@apply flex items-center justify-center
				@apply border-0
				@apply transition-all
				@apply bg-primary-400 rounded-none
				@media (min-width: 1280px)
					width: setClamp(40)
					height: setClamp(40)
			&.active,&:hover
				a
					@apply bg-secondary-400
		li
			&.next-page,&.prev-page
				flex: 0 0 setClamp(8)
				width: setClamp(8)
				height: setClamp(14)
				@apply bg-contain bg-center bg-no-repeat
				@apply transition-all
				a
					@apply text-0 block
					@apply w-full h-full
			&.prev-page
				background-image: url(../img/icon/arrow_back_ios.svg)
			&.next-page
				background-image: url(../img/icon/arrow_next_ios.svg)
			a
				@apply text-16px
			&.active,&:hover
				&.prev-page
					background-image: url(../img/icon/arrow_back_ios-hover.svg)
				&.next-page
					background-image: url(../img/icon/arrow_next_ios-hover.svg)
				// a
				// 	@apply text-primary-700

