footer
	@media (max-width: 1023.98px)
		@apply pt-8
footer
	@apply text-white
	.socials
		i
			@apply text-20px font-normal text-white
	.wrapper
		@apply grid lg:grid-cols-3 md:grid-cols-2 grid-cols-1 xl:gap-20 gap-10
		.footer-column
			.title
				@apply pb-3 border-b border-b-white/30 mb-3
			.footer-social
				@apply mt-3
				ul
					@apply flex items-center gap-3
					li
						a
							@apply rem:w-[50px] rem:h-[50px] rounded-full
							@apply inline-flex items-center justify-center bg-[#1025A1]
			.footer-contact 
				ul
					@apply flex flex-col gap-3
					li
						a
							@apply inline-flex items-center gap-2
							@apply border-b border-b-transparent transition-all ease-in-out
						&:hover
							a
								@apply border-b-white
			ul.footer-menu 
				@apply grid grid-cols-2 gap-x-10 gap-y-3 pl-6
				@apply pb-3 border-b border-b-white/30 mb-3
				li
					@apply list-disc
					a
						@apply border-b border-b-transparent transition-all ease-in-out
					&:hover
						a
							@apply border-b-white
			.dowload-file 
				a
					@apply flex items-center gap-2
	.footer-bottom
		@screen -lg
			@apply flex-col gap-5
		.footer-copyright
			@screen -md
				@apply text-center
		ul
			@apply flex items-center
			li
				&+li
					&::before
						content: '|'
						@apply text-white
						@apply mx-5
				a
					@apply text-14px font-normal