.arrow-button
	--data-length: 36px
	@media (min-width: 576px)
		--data-length: 40px
	@media (min-width: 1280px)
		--data-length: clamp(40px,calc(40/1920*100rem),calc(40/1920*100rem))
	.swiper-button-prev
		left: r(-30)
		@media (min-width: 1280px)
			left: -40px
		@media (min-width: 1600px)
			left: setClamp(-80)
		@media (min-width: 1700px)
			left: setClamp(-94)
		@media (min-width: 2000px)
			left: r(-96)
		&::after
			content: "\f060"
			@apply font-Awesome6 text-xl font-light text-white

	.swiper-button-next
		right: r(-30)
		@media (min-width: 1280px)
			right: -40px
		@media (min-width: 1600px)
			right: setClamp(-80)
		@media (min-width: 1700px)
			right: setClamp(-94)
		@media (min-width: 2000px)
			right: r(-96)
		&::after
			content: "\f061"
			@apply font-Awesome6 text-xl font-light text-white

	.swiper-button-prev,.swiper-button-next
		margin: 0
		top: 50%
		transform: translateY(-50%)
		background: url(../img/btn-bg.svg) no-repeat center center
		@apply h-10 rem:w-[50px]
		@apply bg-transparent
		@apply transition-all rounded-none
		@apply overflow-hidden
		@apply border-0
		@apply bg-transparent z-3 
		
		@media (min-width: 1280px)
			&:hover
				background: url(../img/btn-bg-active.svg) no-repeat center center
				&::before
					@apply bg-primary-500
	&.arrow-2
		--data-length: 32px
		.swiper-button-prev
			&::after
				background-image: url(../img/product/arrow-left.svg)
		.swiper-button-next
			&::after
				background-image: url(../img/product/arrow-right.svg)
		.swiper-button-prev,.swiper-button-next
			@apply rounded-full
			&::after
				@apply inset-1.5
			&::before
				@apply bg-white rounded-full
