	@media (max-width: 1279.98px)
		display: flex
		justify-content: center
		align-items: center
		position: relative
		.swiper-button-prev,.swiper-button-next
			margin-top: 24px
			position: static
			transform: translateY(0)
		.swiper-button-next
			margin-left: 8px
	&.no-responsive
		@media (max-width: 1280px)
			display: contents
		.swiper-button-prev,.swiper-button-next
			position: absolute !important
			margin: 0 !important
			top: 50%
			transform: translateY(-50%)
		.swiper-button-prev
			@media (max-width: 1279.98px)
				@apply left-5px
		.swiper-button-next
			@media (max-width: 1279.98px)
				@apply right-5px
	&.close-arrow
		@apply flex justify-start
		.swiper-button-prev,.swiper-button-next
			@apply static translate-y-0
			@media (max-width: 1279.98px)
				@apply mt-0
		.swiper-button-next
			@apply ml-3 xl:ml-9
	&.inline-arrow
		@apply relative
		@apply flex justify-between w-full
		&::before
			content: ''
			height: 1px
			width: calc(100% - calc(var(--data-length)*2))
			@apply bg-ccc
			@apply absolute top-1/2 -translate-y-1/2 left-1/2 -translate-x-1/2
			@media (min-width: 1280px)
				height: setClamp(1)
		.swiper-button-prev,.swiper-button-next
			@apply static translate-y-0 bg-white
			@media (min-width: 1280px)
				&:hover
					@apply scale-100
		.swiper-button-next
			@apply ml-3 xl:ml-9
	&.arrow-button
		.swiper-button-prev,.swiper-button-next
			&.swiper-button-disabled
				@apply opacity-30 pointer-events-none
				&::before
					@apply bg-neutral-950

.arrow-container-inline
	@apply flex items-center justify-between flex-wrap gap-3
	.arrow-button
		@apply mt-0
		.swiper-button-prev,.swiper-button-next
			flex: 0 0 setClamp(15)
			width: setClamp(15)
			height: setClamp(24)
			@apply rounded-none
			@media (max-width: 1279.98px)
				@apply mt-0
			@media (max-width: 575.98px)
				flex: 0 0 setClamp(28)
				width: setClamp(28)
				height: setClamp(28)
				@apply bg-white shadow-md
			&::after
				@apply inset-0
				@apply bg-transparent
				@media (max-width: 575.98px)
					@apply inset-1
			&::before
				@apply bg-transparent
			&:hover
				&::after
					@apply scale-110
		.swiper-button-prev
			&::after
				background-image: url(../img/slide/chevron-left-small.svg)
		.swiper-button-next
			@apply ml-10px
			&::after
				background-image: url(../img/slide/chevron-right-small.svg)

.swiper-pagination
	@apply pointer-events-none
	&.swiper-pagination-bullets
		&.swiper-pagination-horizontal
			line-height: 0
			@apply p-0 static mx-auto
			@apply gap-2 lg:gap-4 w-full flex justify-center
			span
				@apply w-8 lg:w-50px h-0.5 rounded-none bg-white/30 border-0
				@apply m-0
				&[class*='active']
					@apply bg-white

.swiper-column-auto
	// Minus 8px every screen under 1280
	.swiper-slide.swiper-slide
		@apply overflow-visible
	.swiper-slide
		@apply w-full h-auto
		@apply mr-5 3xl:mr-10
		&:last-child
			@apply mr-0
	// [dir="rtl"]
	// 	.swiper-slide
	// 		@apply mr-0 sm:mr-0 md:mr-0 xl:mr-0
	// 		@apply ml-3 sm:ml-4 md:ml-6 xl:ml-10
	// 		&:last-child
	// 			@apply ml-0
	&.auto-2-column
		.swiper-slide
			width: 75%
			@media (min-width: 576px)
				width: calc(50% - clamp(16px,calc(16/1920*100rem),calc(16/1920*100rem)))
			@media (min-width: 1024px)
				width: calc(50% - clamp(16px,calc(16/1920*100rem),calc(16/1920*100rem)))
			@media (min-width: 1280px)
				width: calc(50% - clamp(27px,calc(27/1920*100rem),calc(27/1920*100rem)))
			@media (min-width: 1700px)
				width: calc(680/1400*100%)
	&.auto-3-column
		.swiper-slide
			width: 100%
			@media (min-width: 576px)
				width: calc(50% - 10px)
			@media (min-width: 1024px)
				width: calc(33.3333% - 20px)
			@media (min-width: 1280px)
				width: calc(413.3333/1280*100%)
		// &.auto-3-column-wide
		// 	.swiper-slide
		// 		@media (min-width: 1280px)
		// 			width: calc((100vw - clamp(55px,calc(55/1920*100rem),calc(55/1920*100rem))*2 - clamp(32px,calc(32/1920*100rem),calc(32/1920*100rem))*2)/3)

	&.auto-4-column
		.swiper-slide
			width: 100%
			@media (min-width: 576px)
				width: calc(50% - clamp(8px,calc(8/1920*100rem),calc(8/1920*100rem)))
			@media (min-width: 768px)
				width: calc(50% - 10px)
			@media (min-width: 1024px)
				width: calc(calc(100vw - 24px - 32px)/3)
			@media (min-width: 1280px)
				width: calc(25vw - 39px)
			@media (min-width: 1600px)
				width: calc(320/1400*100%)
	&.show-half-mobile
		@media (max-width: 575.98px)
			.swiper-slide
				width: 75%

	&.allow-touchMove
		.swiper-slide
			@apply cursor-grab

.swiper-column-1,.swiper-column-2,.swiper-xl-3,.swiper-xl-4
	.swiper-slide
		@apply h-auto

.swiper-center
	.swiper-wrapper
		@apply w-fit mx-auto

.swiper-shadow-spacing
	@media (min-width: 1280px)
		.swiper
			@apply px-4 -mx-4
			@apply py-4 -my-4
