.button
	user-select: none
	*
		@media (min-width: 576px)
			white-space: nowrap
	.btn
		@apply font-semibold
		@apply leading-1
		@apply rem:h-[40px] px-4
		@apply inline-flex items-center justify-center
		@apply uppercase
		@apply text-16px sm:text-18px font-normal text-white
		@apply  gap-2
		@apply transition-all cursor-pointer
		
		&.phone-icon
			&::after
				content: "\f095"
	.btn-primary
		@apply bg-primary-800 relative
		&::before
			content: ""
			@apply rem:border-t-[20px] rem:border-b-[20px] border-t-transparent border-b-transparent
			@apply absolute right-full top-0 w-0 h-full rem:border-r-[20px] border-r-primary-800
			@apply rem:rounded-l-sm transition-all
		&::after
			content: ""
			@apply rem:border-t-[20px] rem:border-b-[20px] border-t-transparent border-b-transparent
			@apply absolute left-full top-0 w-0 h-full rem:border-l-[20px] border-l-primary-800
			@apply rem:rounded-r-3 transition-all
		&:hover
			@apply bg-primary-500
			&::before
				@apply border-r-primary-500
			&::after
				@apply border-l-primary-500
		&::after
			@apply text-white
		&.white
			@apply bg-white text-primary-500 font-bold
			&::before
				@apply border-r-white
			&::after
				@apply border-l-white
			&:hover
				@apply bg-primary-800 text-white
				&::before
					@apply border-r-primary-800
				&::after
					@apply border-l-primary-800
	.btn-secondary
		@apply bg-white text-primary-500
		&::after
			@apply text-white
		&:hover
			@apply bg-primary-500 text-white
			&::after
				@apply text-white
