.zoom-img
	overflow: hidden
	@media (min-width: 1280px)
		&:hover
			img,svg
				transform: scale(1.05)
	img,svg
		transition: .3s all ease-in-out

.zoom-img-rotate
	overflow: hidden
	&:hover
		img
			transform: scale(1.05) rotate(2deg)
	img
		transition: .3s all ease-in-out

@keyframes blink_cursor
	from
		opacity: 0
	to
		opacity: 100

@keyframes cloud
	0%
		margin-left: 0
	100%
		margin-left: -100vw

@keyframes beforeThumbShow
	0%
		width: calc(100% * var(--data-delay-clean) + clamp(12px,calc(12/1920*100rem),calc(12/1920*100rem)))
		// left: calc(100% * var(--data-delay-clean) + clamp(12px,calc(12/1920*100rem),calc(12/1920*100rem)))
		opacity: 100
	70%
		opacity: 100
	100%
		// left: 0
		width: 100%
		opacity: 0
@keyframes thumbShow
	0%
		margin-right: 100%
		opacity: 0
	100%
		margin-right: 0
		opacity: 100

@keyframes scale-in-center
	0%
		-webkit-transform: scale(0)
		transform: scale(0)
		opacity: 1

	100%
		-webkit-transform: scale(1)
		transform: scale(1)
		opacity: 1

@keyframes slide-in-blurred-bottom
	0%
		-webkit-transform: translateY(1000px) scaleY(2.5) scaleX(0.2)
		transform: translateY(1000px) scaleY(2.5) scaleX(0.2)
		-webkit-transform-origin: 50% 100%
		transform-origin: 50% 100%
		-webkit-filter: blur(40px)
		filter: blur(40px)
		opacity: 0

	100%
		-webkit-transform: translateY(0) scaleY(1) scaleX(1)
		transform: translateY(0) scaleY(1) scaleX(1)
		-webkit-transform-origin: 50% 50%
		transform-origin: 50% 50%
		-webkit-filter: blur(0)
		filter: blur(0)
		opacity: 1

@keyframes ping
	0%
		transform: scale(0.2)
		opacity: 0.8

	80%
		transform: scale(1.2)
		opacity: 0.09

	100%
		transform: scale(2.2)
		opacity: 0

@keyframes slide-in-blurred-right
	0%
		-webkit-transform: translateX(1000px) scaleX(2.5) scaleY(0.2)
		transform: translateX(1000px) scaleX(2.5) scaleY(0.2)
		-webkit-transform-origin: 0% 50%
		transform-origin: 0% 50%
		-webkit-filter: blur(40px)
		filter: blur(40px)
		opacity: 0

	100%
		-webkit-transform: translateX(0) scaleY(1) scaleX(1)
		transform: translateX(0) scaleY(1) scaleX(1)
		-webkit-transform-origin: 50% 50%
		transform-origin: 50% 50%
		-webkit-filter: blur(0)
		filter: blur(0)
		opacity: 1

@keyframes flip-in-ver-right
	0%
		-webkit-transform: rotateY(-80deg)
		transform: rotateY(-80deg)
		opacity: 0

	100%
		-webkit-transform: rotateY(0)
		transform: rotateY(0)
		opacity: 1

@keyframes slide-in-blurred-top
	0%
		transform: translateY(-1000px) scaleY(2.5) scaleX(0.2)
		transform-origin: 50% 0%
		filter: blur(40px)
		opacity: 0
	100%
		transform: translateY(0) scaleY(1) scaleX(1)
		transform-origin: 50% 50%
		filter: blur(0)
		opacity: 1

@keyframes dot-pulse-before
	0%
		box-shadow: 9984px 0 0 -5px

	30%
		box-shadow: 9984px 0 0 2px

	60%,
	100%
		box-shadow: 9984px 0 0 -5px

@keyframes dot-pulse
	0%
		box-shadow: 9999px 0 0 -5px

	30%
		box-shadow: 9999px 0 0 2px

	60%,
	100%
		box-shadow: 9999px 0 0 -5px

@keyframes dot-pulse-after
	0%
		box-shadow: 10014px 0 0 -5px

	30%
		box-shadow: 10014px 0 0 2px

	60%,
	100%
		box-shadow: 10014px 0 0 -5px

@keyframes flip-in-ver-left
	0%
		-webkit-transform: rotateY(80deg)
		transform: rotateY(80deg)
		opacity: 0

	100%
		-webkit-transform: rotateY(0)
		transform: rotateY(0)
		opacity: 1

@keyframes flip-in-ver-right
	0%
		-webkit-transform: rotateY(-80deg)
		transform: rotateY(-80deg)
		opacity: 0

	100%
		-webkit-transform: rotateY(0)
		transform: rotateY(0)
		opacity: 1

@keyframes float
	0%
		// box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6)
		transform: translatey(0)

	50%
		box-shadow: 0 setClamp(10) setClamp(10) 0 rgba(0,0,0,0.2)
		transform: translatey(setClamp(-20))

	100%
		// box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6)
		transform: translatey(0)

@keyframes pulseShow
	from
		opacity: 0.3
	to
		opacity: 1
