.popup-product-form
	&.fancybox__container
		.fancybox__viewport
			.fancybox__content
				@apply max-w-clamp-946px px-4 py-8 lg:p-10
				@apply w-full
	.wrapper
		gap: 24px
		@media (min-width: 1024px)
			gap: calc(40/866*100%)
		.col-left
			@media (min-width: 1024px)
				flex: 0 0 calc(382/866*100%)
		.img
			a
				+img-ratio(570/382)
				@apply rounded-tl-5 rounded-br-5

	.form-wrap
		@apply space-y-5
	.form-group
		input,textarea
			@apply block w-full appearance-none resize-none
			@apply text-16px placeholder:text-neutral-700 text-neutral-900
			@apply py-3 px-5 border border-neutral-100 rounded-4px
		textarea
			height: setClamp(140)
.has-custom-close-button
	&.fancybox__container
		.fancybox__viewport
			.fancybox__content
				[data-fancybox-close]
					// outline: 0 !important
					box-shadow: none !important
					border: 0
					width: setClamp(32)
					height: setClamp(32)
					top: 0
					right: 0
					@apply focus:outline-white outline outline-transparent
					@apply rounded-sm
					@apply bg-neutral-300
					@apply transition-all opacity-100
					@apply flex items-center justify-center
					@apply p-2
					svg
						@apply w-full h-full
						@apply stroke-transparent
					&:hover
						@apply bg-text2 shadow-lg
						svg
							@apply text-white
.has-custom-close-normal-button
	&.fancybox__container
		.fancybox__viewport
			.fancybox__content
				[data-fancybox-close]
					top: 0
					right: 0
					// outline: 0 !important
					border: 0 !important
					box-shadow: none !important
					width: 40px
					height: 40px
					padding: 0
					@apply transition-all opacity-100 bg-transparent
					@apply rounded-5px bg-transparent
					@media (min-width: 1280px)
						width: setClamp(40)
						height: setClamp(40)
						@apply top-3 right-3
					i
						@apply header-32 text-primary-800
					&:hover
						@apply bg-ccc

.fancybox__container.popup-mycasa-layout
	.fancybox__viewport
		.fancybox__content
			@apply max-w-clamp-845px rounded-3 bg-white
			@apply w-full py-4 md:py-8 px-3 md:px-4
			[data-fancybox-close]
				@apply hidden
	.box-customer
		@media (max-width: 767.98px)
			@apply p-0
		.ctm-review
			@apply line-clamp-none
	.popup-content-wrapper
		@apply py-4 md:p-6
	.list-hidden-facilities
		.item
			max-width: calc(358/791*100%)
			.icon
				flex: 0 0 setClamp(36)
				width: setClamp(36)
				a
					+img-ratio(1/1,contain)
	#popup-write-review
		@apply bg-neutral-700
		.box-booking
			.gallery
				@media (max-width: 767.98px)
					@apply hidden
		.form-group
			textarea
				height: setClamp(232)
			textarea,input
				@apply block rounded-4 bg-white border border-neutral-400
				@apply p-3 md:p-6
				@apply w-full appearance-none resize-none
	.box-rating
		.star
			flex: 0 0 setClamp(24)
			width: setClamp(24)
			animation: flip-in-ver-right 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both
			&.active
				animation: flip-in-ver-left 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both
				path
					@apply fill-primary-500
			a
				+img-ratio(1/1)

.fancybox__container.fancybox__container
	.fancybox__viewport
		.fancybox__content
			@apply rounded-3 bg-white border border-neutral-400
			// @apply overflow-hidden
.custom-popup-content
	&.fancybox__container
		.fancybox__viewport
			.fancybox__content
				max-width: setClamp(1200)
				padding: setClamp(40) setClamp(12)
				@media (min-width: 768px)
					padding: setClamp(50) setClamp(20)

[data-fancybox]
	@apply cursor-pointer
.popup-subscribe-form
	&.fancybox__container
		.fancybox__viewport
			.fancybox__content
				max-width: setClamp(1196)
				padding: 40px 12px
				@media (min-width: 576px)
					padding: 50px 20px
				@apply w-full
				@media (min-width: 768px)
					padding: 50px 40px
				@media (min-width: 1280px)
					padding: setClamp(60) setClamp(153)
		.form-wrap
			@apply grid md:grid-cols-2
			@apply xl:gap-x-30px gap-y-3
			@apply gap-x-3
		.form-group
			textarea,input,select
				@apply w-full
				@apply py-10px px-5
				@apply rounded-4px
				@apply text-16px text-text9
				@apply bg-[#FAFAFA]

			textarea
				height: setClamp(116)
				@apply resize-none
			select
				background-image: url(../img/icon/select-dropdown.svg)
				background-size: setClamp(14) setClamp(24)
				background-position: center right setClamp(20)
				@apply bg-no-repeat appearance-none
		.form-submit
			@apply mt-6 xl:mt-10
			button
				height: setClamp(40)
				padding: 0 r(45)
				background-color: var(--data-color,#ec2028)
				@apply w-fit
				@apply block
				@apply text-white
				@apply rounded-5px
				@apply mx-auto

.fancybox__container
	.fancybox__slide.has-youtube
		.fancybox__content
			max-width: 1176px
			width: 100% !important

.fancybox__container
	.fancybox__carousel
		.fancybox__viewport
			.fancybox__track
				.fancybox__slide.has-pdf
					@apply p-0
					.fancybox__content
						@apply h-full w-full pt-10

						[data-fancybox-close]
							top: 0
							right: 0
							// outline: 0 !important
							border: 0 !important
							box-shadow: none !important
							width: 30px
							height: 30px
							padding: 0
							@apply transition-all opacity-100 bg-transparent
							@apply rounded-none rounded-tr-5px bg-primary-400
							@media (min-width: 1280px)
								width: r(30)
								height: r(30)
							i
								@apply text-xl text-white
							&:hover
								@apply bg-ccc
