header
	&.header-active
		box-shadow: setClamp(4) setClamp(4) setClamp(32) setClamp(16) rgba(0, 0, 0, 0.08)
	&::before
		content: ''
		backdrop-filter: blur(6.900000095367432px)
		@apply absolute inset-0 bg-white/90 -z-1
	@media (min-width: 1280px)
		height: setClamp(102) !important
	.col-left
		flex: 0 0 setClamp(114)
		width: setClamp(114)
		.logo
			a
				@apply w-full
				@media (min-width: 1280px)
					+img-ratio(96/96)
				@media (max-width: 1279.98px)
					@apply h-full
				img
					@apply object-contain
					@media (max-width: 1279.98px)
						@apply w-auto h-full object-contain
	.bottom
		nav
			a
				@apply text-18px font-bold text-primary-1
				@apply relative z-1 transition-all uppercase h-10 inline-flex items-center justify-center px-4
				&::before
					content: ""
					@apply rem:border-t-[20px] rem:border-b-[20px] border-t-transparent border-b-transparent
					@apply absolute right-full top-0 w-0 h-0 rem:border-r-[20px] border-r-primary-800
					@apply rem:rounded-l-sm transition-all opacity-0
				&::after
					content: ""
					@apply rem:border-t-[20px] rem:border-b-[20px] border-t-transparent border-b-transparent
					@apply absolute left-full top-0 w-0 h-0 rem:border-l-[20px] border-l-primary-800
					@apply rem:rounded-r-3 transition-all opacity-0
			>ul
				@apply flex items-center gap-8
				>li
					>.title i
						@apply hidden

					// &:first-child
					// 	>a
					// 		@apply text-0 w-10
					// 		&::after
					// 			content: "\f015"
					// 			@apply font-Awesome6 text-18px font-light
					&[class*='has-children']
						@apply relative
						>ul
							width: setClamp(210)
							@apply absolute
							@apply opacity-0 pointer-events-none
							@apply top-full
							@apply transition-all
							@apply bg-white
							@apply flex flex-col gap-2
							@apply py-2
							@apply shadow-lg
							@apply left-0
							a
								@apply text-16px font-medium
							li
								&:hover,&[class*='current']
									>a
										@apply bg-primary-800 text-white
					&[class*='current'],&:hover
						>a,>.title >a
							@apply text-white bg-primary-800
							&::before
								@apply opacity-100
							&::after
								@apply opacity-100
					&:hover
						>ul
							@apply opacity-100 pointer-events-auto
					>ul
						>li
							&[class*='has-children']
								>ul
									@apply left-full top-0 -translate-y-8

	.search-wrapper
		@media (min-width: 1280px)
			width: setClamp(260)
		.searchbox
			@apply w-full h-full bg-neutral-50
			@apply px-3 xl:px-6 py-2 xl:py-3px flex items-center gap-2
			input
				@apply w-full text-neutral-500 flex-1
				@apply bg-transparent text-16px leading-1.25 text-neutral-500-main
			button
				flex: 0 0 setClamp(16)
				width: setClamp(16)
	.search-overlay
		@media (max-width: 1279.98px)
			@apply absolute top-full left-0 w-full
			@apply opacity-0 -translate-y-1/2 transition-all duration-300
			&.active
				@apply opacity-100 translate-y-0
	.language
		.wpml-ls
			@apply border-0 p-0
		ul
			@apply flex items-center
			@apply rounded-br-6 rounded-tl-6
			@apply border-2 border-primary-800 overflow-hidden
		li
			a
				@apply flex items-center justify-center overflow-hidden
				@apply transition-all text-18px text-neutral-500 p-0
				@apply text-14px px-10px py-5px
				@apply block text-primary-800
				img
					@apply w-full h-full object-cover
			&[class*='current']
				a
					@apply pointer-events-none
					@apply bg-primary-800 text-white
	%full-fill
		position: absolute
		top: 0
		bottom: 0
		left: 0
		right: 0
	$anime-time: 4s
	$clip-distance: .05
	$path-width: 2px
	$main-color: #056B5A
	$box-size: 36px
	$clip-size: $box-size * (1 + $clip-distance * 2)
	#burger
		flex: 0 0 36px
		width: 36px
		height: 36px
		color: $main-color
		// box-shadow: inset 0 0 0 1px rgba($main-color, .1)
		svg
			transition: transform .6s cubic-bezier(0.4, 0, 0.2, 1)
			@apply h-full
		.line
			fill: none
			stroke-linecap: round
			stroke-linejoin: round
			stroke-width: 2
			transition: stroke-dasharray .6s cubic-bezier(0.4, 0, 0.2, 1),stroke-dashoffset .6s cubic-bezier(0.4, 0, 0.2, 1)
			@apply stroke-primary-800
		.line-top-bottom
			stroke-dasharray: 12 63
		&.active
			svg
				transform: rotate(-45deg)
				.line-top-bottom
					stroke-dasharray: 20 300
					stroke-dashoffset: -32.42
	.nav-mobile
		transition: all .7s cubic-bezier(0.53,-0.22,0.35,1.16)
		max-width: 375px
		height: calc(100dvh - var(--header-height))
		@apply opacity-0 pointer-events-none
		@apply translate-x-full
		@apply -top--header-height shadow-lg
		&.active
			@apply translate-x-0
			@apply opacity-100 pointer-events-auto
		nav
			@apply flex-1 h-full flex flex-col overflow-auto
		nav
			@apply select-none pt-6 pb-2
			a
				@apply subheader-24 sm:text-32px font-medium text-black
				@apply py-2
			>ul
				>li
					// &:not(:first-child)
					// 	@apply border-t border-t-neutral-400
					&:first-child
						>a
							@apply text-0
							&::after
								content: "\f015"
								@apply font-Awesome6 subheader-24 sm:text-32px font-medium
					>a, >.title
						@apply flex items-center gap-1
						@apply transition-all duration-300
						@apply w-fit mx-auto
					&[class*="current"]
						>a
							@apply text-primary-800
							@apply underline
					>ul
						@apply bg-neutral-100 hidden
						@apply border-b border-b-primary-600
						li
							&[class*="current"]
								>a
									@apply text-primary-800

						a,.title >a
							@apply text-base block text-black
							@apply py-2 px-4 text-center
		.hotline
			@apply flex-1
