.contact
	.icon
		i
			@apply text-20px leading-normal text-primary-800
	.map
		+img-ratio(252/480,none)
		@apply rounded-3
	.form-group
		input,textarea
			@apply block w-full appearance-none resize-none
			@apply text-16px placeholder:text-neutral-700 text-neutral-900
			@apply py-3 px-5 border border-neutral-100 rounded-4px
		textarea
			height: setClamp(140)
