.introduce-3
	.icon
		a
			+img-ratio(1/1)

	.item
		.description
			@apply text-neutral-900
		@media (min-width: 1024px)
			&:nth-of-type(1)
				padding-top: r(140)
			&:nth-of-type(2)
				padding-top: r(196)
		&:nth-of-type(1)
			@apply bg-[#E3C097]
		&:nth-of-type(2)
			@apply bg-[#F6F3E1]
		&:nth-of-type(3)
			@apply text-white
			@apply bg-primary-800
			.description
				@apply text-white
	.description
		@media (max-width: 1023.98px)
			max-height: 200px
			@apply overflow-auto
